@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fade-out {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes checked-anim {
  0% {
    width: 100%; }
  100% {
    width: 20%; } }

@keyframes not-checked-anim {
  0% {
    width: 20%;
    opacity: 1; }
  100% {
    width: 100%;
    opacity: 0; } }

@keyframes slide {
  100% {
    left: 0; } }

@keyframes roll-in {
  0% {
    transform: translateY(-1.5rem);
    opacity: 0;
    visibility: hidden; }
  100% {
    transform: translateY(0);
    opacity: 1;
    visibility: visible; } }

@keyframes roll-out {
  0% {
    transform: translateY(0);
    opacity: 1;
    visibility: visible; }
  100% {
    transform: translateY(-1.5rem);
    opacity: 0;
    visibility: hidden; } }

@keyframes move-in {
